.loginContainer {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    z-index: 3;
    display: flex;
    justify-content: center;
    align-items: center;
}

.overlay {
    width: 100%;
    height: 100%;
    opacity: 0.4;
    background-color: var(--dark-grey);
    position: absolute;
    z-index: 1;
}

.loginForm {
    width: 555px;
    height: 568px;
    box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.03);
    background-color: var(--white);
    position: absolute;
    z-index: 2;
    display: flex;
    align-items: center;
    flex-flow: column;
}

img.crossIcon {
    width: 24px;
    height: 24px;
    object-fit: contain;
    object-position: center;
    position: absolute;
    top: 14px;
    right: 14px;
    cursor: pointer;
}

img.crossIcon:active {
    opacity: .7;
}

span.loginBoxTitle {
    width: 105px;
    height: 42px;
    margin-top: 45px;
    font-family: Montserrat;
    font-size: 32px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.31;
    letter-spacing: normal;
    text-align: center;
    color: var(--dark-grey);
    position: relative;
    display: flex;
    justify-content: center;
}

span.validateMessage {
    width: 191px;
    height: 22px;
    font-family: Montserrat;
    font-size: 12px;
    font-weight: 500;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.83;
    letter-spacing: normal;
    text-align: center;
    position: absolute;
    top: calc(100% + 9px);
    color: var(--white)
}

.showValidate {
    color: var(--strawberry) !important;
}

div.loginBoxContent {
    width: 395px;
    display: flex;
    flex-direction: column;
    margin-top: 46px;
}

label.loginBoxLabel {
    width: 39px;
    height: 18px;
    font-family: Montserrat;
    font-size: 12px;
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.5;
    letter-spacing: normal;
    color: var(--dark-grey);
    text-transform: uppercase;
}

/* selection box */
div.selectionBox {
    display: flex;
}

div.rememberBox {
    flex: 0 0 50%;
    display: flex;
    align-items: center;
    cursor: pointer;
}

img.checkBoxIcon {
    width: 24px;
    height: 24px;
    object-fit: contain;
    object-position: center;
}

div.blankBox {
    width: 10px;
    height: 10px;
    border: 3px solid var(--pale-orange-40);
    margin: 4px;
}

div.fogotPasswordButton {
    flex: 1;
}   

span.rememberTitle {
    width: 152px;
    height: 22px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.57;
    letter-spacing: normal;
    color: var(--greyish-brown);
    margin-left: 2px;
}

div.fogotPasswordBox {
    flex: auto;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

span.fogotPasswordButton {
    width: 166px;
    height: 22px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 600;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.57;
    letter-spacing: normal;
    text-align: right;
    color: var(--greyish-brown);
    cursor: pointer;
}

.spanBox {
    height: 66px;
    width: 284px;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: 1.57;
    letter-spacing: normal;
    text-align: center;
    color: var(--dark-grey);
    margin-top: 63px;
    border-top: 1px solid var(--white-three) ;
    display:flex;
    justify-content: center;
    align-items: center;
}

.spanBox > .highlight {
    font-weight: bold;
    color: var(--pumpkin-orange);
    text-decoration: underline;
    text-underline-offset: 4px;
} 



